.navigation-link{

    display: flex;
    flex-wrap: wrap;
    max-width: 135px;
    width: 100%;
    gap: 10px;
    margin: 5px 0 15px;


  &__link{
    color: #4F5B7C;
    font-size: 16px;
    font-style: var(--normal);
    font-weight: var(--font500);
    line-height: 168.7%;
  }

  &__delimiter{
    color: #4F5B7C;
    font-size: 16px;
    font-style: var(--normal);
    font-weight: var(--font500);
    line-height: 168.7%;
  }

  &__link--active{
    color: var(--white);
  }
}
